//--------------------------------------------------------------
// TRANSITIONS
//--------------------------------------------------------------

////
/// @group variables
/// @author WebDevStudios
////

// Opacity transition.
$transition-opacity: opacity 0.5s ease;

// Border transition
$transition-border: border 0.2s ease-in-out;

// All transitions. Use only in emergencies!
$transition-all: all 0.5s ease-in-out;

// Transform transitions.
$transition-transform: transform 0.5s ease-in-out;

// Transform Hamburger
$transition-hamburger: transform 0.5s, background-color 0.3s ease-in-out;

// Transform Background Color
$transition-bg-color: background-color 0.3s ease-in-out;

// Transform Visibility
$transition-visibility: visibility 0.3s ease-in-out;

// Transform Max Height
$transition-max-height: max-height 0.6s ease-in-out;
